import React from "react";
import "./CreateReportTerm.scss";

import { reportTermArr } from "../../../config/getAPI";

import { Button } from "../../../components/Button";

export const CreateReportTerm = () => {
  return (
    <div className="create-report-term">
      <h3>Bo co tng kt hc k</h3>
      <div className="score-info">
        <h4>Hc k: 1</h4>
        <h4>Nm hc: 2018-2019</h4>
      </div>
      <div className="container">
        <div className="row heading">
          <div className="item col-25-percent center al-center">Lp</div>
          <div className="item col-25-percent center al-center">S s</div>
          <div className="item col-25-percent center al-center">
            S lng t
          </div>
          <div className="item col-25-percent center al-center">T l</div>
        </div>
        {reportTermArr.map((item) => (
          <div className="row content">
            <div className="item col-25-percent center al-center">
              {item.Class}
            </div>
            <div className="item col-25-percent center al-center">
              {item.Total}
            </div>
            <div className="item col-25-percent center al-center">
              {item.Passed}
            </div>
            <div className="item col-25-percent center al-center">
              {item.Rate}
            </div>
          </div>
        ))}
      </div>
      <div className="btns">
        <Button innerText="Xut kt qu" btnType="export" />
        <Button innerText="In kt qu" btnType="export" />
      </div>
    </div>
  );
};
